import React, {useEffect, useState} from 'react';

import './navbar.css';
import {useTranslation} from "react-i18next";
import 'bootstrap/dist/css/bootstrap.min.css';
import 'flag-icon-css/css/flag-icons.min.css';
import Aos from "aos";
import i18next from "i18next";



const language=[
    {
        code: 'uz',
        name: 'UZ',
        country_code: 'uz'
    },
    {
        code: 'en',
        name: 'EN',
        country_code: 'gb'
    },
    {
        code: 'ru',
        name: 'RU',
        country_code: 'ru'

    }
];

function Navbar(props) {

    useEffect(()=>{
        Aos.init({duration: 300});
    },[]);

    const {t} =useTranslation();
    const [navbar, setNavbar]= useState(false);
    const changeBackground=()=>{
      if (window.scrollY >= 80){
          setNavbar(true)
      }  else {
          setNavbar(false)
      }
    };


    const [toggleMenu,setTogleMenu]=useState(false);

    const toggleNav =()=>{
        setTogleMenu(!toggleMenu)
    };
    const [screenWith, setScreenWith]=useState(window.innerWidth);

    window.addEventListener('scroll', changeBackground);
    useEffect(()=>{
        const changeWith=()=>{
           setScreenWith(window.innerWidth)
        };

        window.addEventListener('resize', changeWith)

    },[]);

    return (
        <>
        <div className="container-fluid">

            <nav className={navbar ? 'navbarActive' : ''}>

                <a href="/" className='logo d-flex'>
                    <img className='Brandimg' src="BrandItcomp4.png" alt=""/>
                    <div className="brandName d-inline-block"> iTComp</div>
                </a>
                <div className='languageMobile'>
                    {
                        language.map(({code, name, country_code})=>(
                            <li key={country_code}>
                                <div onClick={()=>i18next.changeLanguage(code)} className='d-flex'>

                                    {name}
                                </div>
                            </li>
                        ))
                    }
                </div>
                {(toggleMenu || screenWith >900) &&(
                    <ul data-aos="fade-left" className="list">
                        <li onClick={toggleNav} className="items">
                            <a href="#home" >
                                {t('home')}
                            </a>
                        </li>
                        <li onClick={toggleNav} className="items" >
                            <a href="#programmers" >{t('programmers')}</a>
                        </li>
                        <li onClick={toggleNav} className="items">
                            <a href="#projects">{t('projects')}</a>
                        </li>
                        <li onClick={toggleNav} className="items" >
                            <a href="#sevices">{t('services')}</a>
                        </li>
                        <li onClick={toggleNav} className="items" >
                            <a href="#call">{t('contact')}</a>
                        </li>
                    </ul>
                )}
                <div className='language'>
                    {
                        language.map(({code, name, country_code})=>(
                            <li key={country_code}>
                                <div onClick={()=>i18next.changeLanguage(code)} className='d-flex'>
                                    <span className={`flag-icon flag-icon-${country_code}`}></span>
                                    {name}
                                </div>
                            </li>
                        ))
                    }
                </div>

                <div onClick={toggleNav} className=" btn hamburger-menu">

                    <img className='burgermenu' src="burgermenu.png" alt=""/>
                </div>
            </nav>
        </div>
        </>
    );
}

export default Navbar;